import {Component, AfterViewInit} from '@angular/core';
import {FormGroup, FormControl,Validators} from "@angular/forms";
import {DataService} from "./services/data.service";
declare let $:any
declare let firebase:any
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements AfterViewInit{
  public formdatas:FormGroup;
  public sigformdatas:FormGroup;
  private counter:number;

  constructor(public data:DataService){
    this.counter = 0
    this.formdatas = new FormGroup({
      "email" : new FormControl('',[Validators.required,Validators.pattern("[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?")]),
      "password" : new FormControl('',[Validators.required])
    })
    this.sigformdatas = new FormGroup({
      'name' : new FormControl('',[Validators.required]),
      "email" : new FormControl('',[Validators.required,Validators.pattern("[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?")]),
      "password" : new FormControl('',[Validators.required]),
      "repassword" : new FormControl('',[Validators.required])
    })
  }
  ngAfterViewInit(){
    $('.ui.sidebar')
      .sidebar('attach events', '.toc.item')
    ;

    $('#live-chat header').on('click', ()=> {

      if(this.counter%2 !=0)
      {
        console.log(this.counter);
        $("#live-chat").css({
          "width" : "300px"
        })
      }
      else
      {
        $("#live-chat").css({
          "width" : "30%"
        })
      }
      this.counter = this.counter + 1
      $('.chat').slideToggle(300, 'swing');
      $('.chat-message-counter').fadeToggle(300, 'swing');

    });

    $('#live-chat header').click()
  }

  login(){
    $('.ui.modal.loginapp')
      .modal({
        blurring: true
      })
      .modal('show')
    ;
  }
  signup()
  {
    $('.ui.modal.signupapp')
      .modal({
        blurring: true
      })
      .modal('show')
    ;
  }
  match()
  {
    if(this.sigformdatas.get('repassword').value == this.sigformdatas.get("password").value)
    {
      return false
    }
    return true
  }
  finallogin()
  {

    this.data.login(this.formdatas.value)
  }
  finalsignup()
  {
    this.data.signup(this.sigformdatas.value)
  }
  check()
  {

    if(this.data.isAutenticated())
    {

      return true
    }
    else {

     return false
    }
  }





  messagesend()
  {
    firebase.database().ref("details/" + this.data.userkey+"/messages").push({
      message : $("#chatmessage").val(),
      person : "me"

    }).then( () => {
      $("#chatmessage").val("")
    })

  }
}



