import { Injectable } from '@angular/core';
declare let firebase:any
declare let $:any
@Injectable()
export class DataService {
public errormes:boolean = false
  public wrong:any = "somthing"
public sucmes:boolean = false
public items:any = ["mob-cases"]
  public userdet:any
  public userkey:any;
public usermessages:any = []
public userdetails:any = null;
public selectedtype:any = ''
  constructor() { }




 login(user)
 {
   $(".ui .modal").modal('hide')
   firebase.auth().signInWithEmailAndPassword(user.email, user.password).then((data:any)=>
     {

       this.wrong = "SUCCESSFULLY LOGGED IN.";
this.sucmes = true
setTimeout(() => {
  this.sucmes = false
},5000)


       firebase.database().ref().child("details").orderByChild("email").equalTo(user.email).on('value',(snapshot:any)=>{
         console.log(snapshot.val())
         this.userkey = Object.keys(snapshot.val()).map(k => {return k})[0]
         this.userdetails = Object.keys(snapshot.val()).map(k => {return snapshot.val()[k]})[0]
         this.wrong = "HELLO " + this.userdetails.name.toUpperCase();
         this.sucmes = true
         setTimeout(() => {
           this.sucmes = false
         },5000)
       })
     }
   )
     .catch((error:any)=> {
       let errorCode = error.code;
       let errorMessage = error.message;
       if (errorCode === 'auth/wrong-password') {
         this.wrong = "Wrong Password";
         console.log(this.wrong);
         this.wrong = this.wrong.toUpperCase()
         this.errormes = true

       } else {
         this.wrong = errorMessage;
         this.wrong = this.wrong.toUpperCase()
        this.errormes = true
               }
       console.log(error);

       setTimeout(() => {
         this.errormes = false
       },5000)
     });
 }
  logout()
  {
    firebase.auth().signOut().then(()=> {
      this.wrong = "SUCCESSFULLY LOGGED OUT.";
      this.sucmes = true
      setTimeout(() => {
        this.sucmes = false
      },5000)
this.usermessages = [];
      this.userdetails = {};
        this.userkey = ''
    }, (error:any) =>{
      console.log(error);
    });
  }



  signup(user)
  {
    $(".ui .modal").modal('hide')


    firebase.auth().createUserWithEmailAndPassword(user.email,user.password).then((data:any)=>
      {

       this.userkey =  firebase.database().ref("details").push({
         name : user.name,
          email : user.email,
          messages: []
        });
       console.log(this.userkey)
        this.wrong = "SUCCESSFULLY SIGNED UP.";
        this.sucmes = true
        setTimeout(() => {
          this.sucmes = false
          this.wrong = "HELLO " + user.name.toUpperCase();
          this.sucmes = true
          setTimeout(() => {
            this.sucmes = false
          },5000)
        },5000)
      }
    )
      .catch(function (error:any) {
        let errorCode = error.code;
        let errorMessage = error.message;
        if (errorCode == 'auth/weak-password') {
          this.wrong = "Weak Password"
        } else {
          this.wrong = errorMessage
        }
        this.wrong = this.wrong.toUpperCase()
        this.errormes = true
        setTimeout(() => {
          this.errormes = false
        },5000)

      });



  }
  isAutenticated()
  {
    let user = firebase.auth().currentUser;

    if(user) {


      this.userdet = user.email;
if(!this.userdetails)
{
this.userdetails = {}
  firebase.database().ref().child("details").orderByChild("email").equalTo(user.email).on('value',(snapshot)=>{
    this.userkey = Object.keys(snapshot.val()).map(k => {return k})[0]
    this.userdetails = Object.keys(snapshot.val()).map(k => {return snapshot.val()[k]})[0]
    if(this.userdetails.messages) {
      this.usermessages = Object.keys(this.userdetails.messages).map(k => {
        return this.userdetails.messages[k]
      })

    }
    console.log(this.userkey)
    this.wrong = "HELLO " + this.userdetails.name.toUpperCase();
    this.sucmes = true
    setTimeout(() => {
      this.sucmes = false
    },5000)
  })
}

      return true;
    } else {

      return false;
    }
  }
}



