import {Component, OnInit} from '@angular/core';
import {DataService} from "../services/data.service";
import {FormGroup, FormControl,Validators} from "@angular/forms";
declare let $:any
declare let firebase:any
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit{
public formdatas:FormGroup;
public sigformdatas:FormGroup;
public order:FormGroup
  public erromes:boolean = false
  constructor( private data:DataService) {
    this.formdatas = new FormGroup({
      "email" : new FormControl('',[Validators.required,Validators.pattern("[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?")]),
      "password" : new FormControl('',[Validators.required])
    })
    this.sigformdatas = new FormGroup({
      'name' : new FormControl('',[Validators.required]),
      "email" : new FormControl('',[Validators.required,Validators.pattern("[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?")]),
      "password" : new FormControl('',[Validators.required]),
      "repassword" : new FormControl('',[Validators.required])
    })
    this.order = new FormGroup({

    })
  }

  ngOnInit() {

  }
orders(a:any,b:any)
{
  if(a && b)
  {
    $('.ui.basic.test.modal')
      .modal({
        closable  : false,
        onDeny    : ()=>{
          $('.ui.modal.orderhome')
            .modal({
              blurring: true
            })
            .modal('show')
          ;
        },
        onApprove : () => {
          firebase.database().ref("details/" + this.data.userkey+"/orders").push({
            orderid : Math.floor((Math.random() * 100000000000000) + 1),
            type : this.data.selectedtype,
            address :a,
            phonenumber : b
          }).then(()=>
          {
            $(".ui.modal").modal("hide")
            this.data.wrong = this.data.userdetails.name.toUpperCase() + "!! ORDER SUCCESSFULLY PLACED.";
            this.data.sucmes = true
            setTimeout(() => {
              this.data.sucmes = false
            },10000)
          })
        }
      })
      .modal('show')
    ;
  }
  else {
    this.erromes = true
    setTimeout(()=>{
      this.erromes = false
    },5000)
  }
}
login(a:any)
{

  if(this.data.isAutenticated())
  {
    this.data.selectedtype = a
    $('.ui.modal.orderhome')
      .modal({
        blurring: true
      })
      .modal('show')
    ;
  }
  else {
    $('.ui.modal.loginhome')
      .modal({
        blurring: true
      })
      .modal('show')
    ;
  }
}
  signup()
  {
    $('.ui.modal.signuphome')
      .modal({
        blurring: true
      })
      .modal('show')
    ;
  }
  match()
  {
    if(this.sigformdatas.get('repassword').value == this.sigformdatas.get("password").value)
    {
      return false
    }
    return true
  }
  finallogin()
  {

    this.data.login(this.formdatas.value)
  }
  finalsignup()
  {
    this.data.signup(this.sigformdatas.value)
  }
}



